<template>
<div class="main-notes">
<div class="notes" v-for="(note, index) of notes">
    <div class="circle" @click.prevent="editTask(note, index)" :innerHTML="iconcircle"></div>
    <div class="cross" @click.prevent="deleteTask(index)" :innerHTML="iconcross"></div>
    <div class="main-todo">
    <h2 class="note-header">{{ note.header }}</h2>
    <div class="todo" v-for="tasks of note.todo">{{ tasks }}</div>
</div>
</div>
</div>
</template>

<script>
export default {
    data () {
        return {
            iconcross: '<svg version="1.1" id="Capa_1" xmlns="http://www.w3.org/2000/svg" xmlns:xlink="http://www.w3.org/1999/xlink" x="0px" y="0px" viewBox="0 0 612 612" style="enable-background:new 0 0 612 612;" xml:space="preserve"><g><g id="cross"><g><polygon points="612,36.004 576.521,0.603 306,270.608 35.478,0.603 0,36.004 270.522,306.011 0,575.997 35.478,611.397 306,341.411 576.521,611.397 612,575.997 341.459,306.011 " fill="#000000" style="fill: rgb(255, 255, 255);"></polygon></g></g></g></svg>',
            iconcircle: '<svg version="1.1" id="Layer_1" xmlns="http://www.w3.org/2000/svg" xmlns:xlink="http://www.w3.org/1999/xlink" x="0px" y="0px" viewBox="0 0 512 512" style="enable-background:new 0 0 512 512;" xml:space="preserve"><g><g><path d="M311.18,78.008L32.23,356.958L0.613,485.716c-1.771,7.209,0.355,14.818,5.604,20.067 c5.266,5.266,12.88,7.368,20.067,5.604l128.759-31.617l278.95-278.95L311.18,78.008z M40.877,471.123l10.871-44.271l33.4,33.4 L40.877,471.123z" fill="#000000" style="fill: rgb(255, 255, 255);"></path></g></g><g><g><path d="M502.598,86.818L425.182,9.402c-12.536-12.536-32.86-12.536-45.396,0l-30.825,30.825l122.812,122.812l30.825-30.825 C515.134,119.679,515.134,99.354,502.598,86.818z" fill="#000000" style="fill: rgb(255, 255, 255);"></path></g></g></svg>',
            edit:"",
        }
    },
    name: "TaskList",
    props: {
        notes: {
            type:Array,
            required: true,
        }
    },
    methods: {
        deleteTask(index) {
            this.$emit("deleteTask", index)
        },

        editTask(note, index) {
            this.$emit("editTask", note, index)
        }
    }
}
</script>

<style>
.main-notes {
    display: flex;
    justify-content: center;
    align-items: start;
    gap: 20px;
    flex-wrap: wrap;
}

.notes {
    background-color: rgb(255, 255, 255);
    margin-top: 15px;
    padding: 15px;
    border-radius: 15px;
    position: relative;
    display: flex;
    justify-content: center;
}

.circle {
    width: 30px;
    height: 30px;
    border-radius: 50%;
    background-color: #111;
    bottom: -15px;
    position: absolute;
    display: flex;
    justify-content: center;
    align-items: center;
    cursor: pointer;
}

.circle svg {
    width: 12px;
    height: 12px;
}

.cross {
    width: 30px;
    height: 30px;
    border-radius: 50%;
    background-color: #111;
    position: absolute;
    display: flex;
    justify-content: center;
    align-items: center;
    cursor: pointer;
    top: -10px;
    right: -14px;
}

.cross svg {
    width: 12px;
    height: 12px;
}

.note-header {
    border-bottom: 2px solid rgb(32 32 47);
    color: rgb(32 32 47);
}

.todo {
    margin-top: 5px;
    display: none;
    margin-bottom: 10px;
}

.todo:nth-child(-n+6){
    display: block;
}
</style>