<template>
    <TaskList :notes="notes" @editTask="editTask" @deleteTask="deleteTask"></TaskList>
    <PushTask v-if="edit==false" @createTask="createTask"></PushTask>
    <UpdateTask v-if="edit==true" :edditing="edditing" @updateTask="UpdateTask"></UpdateTask>
</template>

<script>
export default {
    data () {
        return {
            notes: [
                {header: 'Планы на понедельник', todo:['Умыться и сделать зарядку', 'Собрать необходимые вещи в зал', 'Пообедать', 'Заняться саморазвитием']},
            ],
            edit: false,
            edditing: [],
            currIndex:""
        }
    },
    methods: {
        createTask(notes) {
            this.notes.push(notes)
        },

        editTask(note, index) {
            this.currIndex = index;
            this.edditing = []
            this.edditing.push(this.notes[index])
            console.log(this.edditing)
            this.edit = true
        },

        UpdateTask(task) {
            this.notes.splice(this.currIndex, 1, task)
            this.edit = false;
        },

        deleteTask(index) {
            this.notes.splice(index);
        }
    }

}
</script>

<style>
* {
    font-family: system-ui, -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}


#app {
    padding: 20px;
    display: flex;
    flex-direction: column;
    align-items: center;
}

body {
    background-color: rgb(233 232 226);
}

</style>