<template>
<form class="update" v-for="task of edditing">
    <h2>Edditing ToDo</h2>
    <input type="text" class="header" v-model="task.header">
    <input type="text" v-for="(note, index) of task.todo" v-model="task.todo[index]">
    <input class="save" @click.prevent="UpdateTask(task)" type="button" value="Сохранить изменения">
</form>
</template>

<script>
export default {
    props: {
        edditing: {
            type:Array,
            required: true
        }
    },
    name: "UpdateTask",
    methods: {
        UpdateTask(task) {
            this.$emit("updateTask", task)
        }
    }
}
</script>

<style>
.update {
    display: flex;
    flex-direction: column;
    margin-top: 45px;
    gap: 15px;
    background-color: rgb(255 255 255);
    padding: 30px;
    border-radius: 15px;
    width: 500px;
    text-align: center;
}

.update input {
    width: 100%;
    background: none;
    border: none;
    border-bottom: 1px solid rgb(17 17 17);
    padding: 5px;
    outline: none;
}

.header {
    font-weight: bold;
    font-size: 25px;
    border: none !important;
    background: rgb(0 0 0 / 2%) !important;
    border-radius: 5px;
}

.save {
    border: 1px solid rgb(17 17 17) !important;
    cursor: pointer;
}
</style>
